.App {
  text-align: center; }

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 70px; }

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white; }

.App-title {
  font-size: 1.5em; }

.App-intro {
  font-size: large; }

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }
#loader {
  background-color: #000;
  z-index: 10;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  color: #FFF; }
  #loader .container {
    position: fixed;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%); }
  #loader p {
    font-size: 20px; }
  #loader .spinner {
    margin: 0 auto;
    -webkit-animation: loader-spin infinite 1s linear;
            animation: loader-spin infinite 1s linear;
    height: 70px;
    width: 70px;
    border: 5px solid #FFF;
    border-radius: 100px; }

#screenshot {
  position: fixed;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  z-index: 1;
  outline: none;
  -webkit-transition: all 0.3s ease;
  -o-transition: all 0.3s ease;
  transition: all 0.3s ease; }

.polaroid {
  border: 10px solid #FFF;
  background: #AE2716;
  border-bottom-width: 50px;
  -webkit-box-shadow: 0px 0px 40px #000;
          box-shadow: 0px 0px 40px #000; }

.webcam {
  background-color: #111;
  position: fixed;
  z-index: 0;
  top: 0px;
  left: 0px;
  /* fixed to left. Replace it by right if you want.*/
  min-width: 100%;
  min-height: 100%;
  width: auto;
  height: auto; }

#captureButton {
  position: fixed;
  z-index: 100;
  bottom: 50px;
  left: 50%;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
  border: 5px solid #FFF;
  border-radius: 100%;
  width: 50px;
  height: 50px;
  -webkit-transition: all 0.3s ease;
  -o-transition: all 0.3s ease;
  transition: all 0.3s ease; }
  #captureButton:hover {
    border-color: #b3b3b3;
    background-color: rgba(179, 179, 179, 0.3);
    cursor: pointer; }
  #captureButton:active {
    border-color: #AE2716;
    -webkit-transition: none;
    -o-transition: none;
    transition: none; }

.hidden {
  display: none; }

@-webkit-keyframes loader-spin {
  0% {
    border-width: 5px; }
  50% {
    border-width: 10px; }
  100% {
    border-width: 5px; } }

@keyframes loader-spin {
  0% {
    border-width: 5px; }
  50% {
    border-width: 10px; }
  100% {
    border-width: 5px; } }
#oldScreenshots {
  z-index: 2;
  position: fixed;
  bottom: 20px;
  right: 20px; }
  #oldScreenshots ul {
    display: inline-block;
    list-style: none; }
    #oldScreenshots ul li {
      display: inline-block;
      position: absolute;
      right: 0;
      bottom: 0; }
  #oldScreenshots .polaroid {
    border-width: 5px;
    border-bottom-width: 20px;
    -webkit-box-shadow: 0px 0px 5px #333;
            box-shadow: 0px 0px 5px #333;
    -webkit-transition: all 0.3s ease;
    -o-transition: all 0.3s ease;
    transition: all 0.3s ease; }

#categories {
  z-index: 100;
  position: fixed;
  top: 20px;
  left: 20px;
  color: #FFF;
  text-align: left;
  text-transform: capitalize;
  text-shadow: 0px 0px 10px #777; }
  #categories .category {
    margin-bottom: 5px; }
    #categories .category:nth-child(1) .probability {
      color: #00FF1B; }
    #categories .category:nth-child(2) .probability {
      color: #C7FF00; }
    #categories .category:nth-child(3) .probability {
      color: #FF7E00; }
    #categories .category:nth-child(4) .probability {
      color: #FF410B; }
    #categories .category:nth-child(5) .probability {
      color: #FF0000; }
  #categories .probability {
    margin-right: 10px;
    min-width: 70px;
    display: inline-block; }

#welcomeText {
  position: fixed;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  z-index: 100;
  color: #FFF;
  text-transform: capitalize;
  text-shadow: 0px 0px 10px #888; }



